<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="parames" label-width="80px">
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.title" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.author" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.publisher" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="parames.categoryIds"
                            multiple
                            placeholder=""
                            style="width:220px"
                        >
                            <el-option
                                v-for="item in categoryOptions"
                                :key="item.categoryId"
                                :label="item.categoryName"
                                :value="item.categoryId"
                            ></el-option>
                        </el-select>
                        <el-button
                            class="button-new-tag"
                            size="small"
                            @click="showCategoryBox"
                        >+ </el-button>
                    </el-form-item>
                    <el-form-item label="">
                        <el-tag
                            :key="tag"
                            v-for="tag in parames.tags"
                            closable
                            :disable-transitions="false"
                            @close="handleClose(tag)"
                        >{{tag}}</el-tag>
                        <el-input
                            class="input-new-tag"
                            v-if="inputVisible"
                            v-model="inputValue"
                            ref="saveTagInput"
                            @keyup.enter.native="handleInputConfirm"
                            @blur="handleInputConfirm"
                        ></el-input>
                        <el-button v-else class="button-new-tag" @click="showInput">+ </el-button>
                    </el-form-item>
                    <el-form-item label="">
                        <images-upload :defaultImage="parames.pictUrl" :index="1" :moreUpload="false" :onSuccess="uploadSuccess"></images-upload>
                    </el-form-item>
                    <el-form-item label="">
                        <images-upload
                            :onSuccess="uploadSuccess"
                            :index="2"
                            :onRemove="removeRemove"
                            :memory="1"
                            :fileList="carousel"
                        ></images-upload>
                    </el-form-item>
                    <el-form-item label="">
                        <images-upload
                            :onSuccess="uploadSuccess"
                            :fileList="imageText"
                            :index="3"
                            :onRemove="removeRemove"
                            :memory="2"
                        ></images-upload>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            v-model="parames.currencyType"
                            placeholder=""
                            style="width:220px"
                        >
                            <el-option
                                v-for="item in moneyOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.reservePrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.finalPrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.balance" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input placeholder="" v-model="parames.quantity" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker
                            value-format="timestamp"
                            v-model="parames.onSaleTime"
                            type="datetime"
                            placeholder=""
                            align="right"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch v-model="noPostage"></el-switch>
                    </el-form-item>
                    <el-form-item label="" v-if="!noPostage">
                        <el-input placeholder="" v-model="parames.freightPrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" v-if="!noPostage">
                        <el-input placeholder="" v-model="parames.freePrice" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="sku">
                        <div>
                            <div class="stand">
                                <ul>
                                    <li class="attr" v-for="(item,index) in standData" :key="index">
                                        <el-button
                                            type="danger"
                                            icon="el-icon-delete"
                                            @click="remove(index)"
                                            style="margin-right:10px"
                                        ></el-button>
                                        <el-input
                                            class="title"
                                            v-model="item.title"
                                            placeholder=""
                                            style="width:100px"
                                            clearable
                                        ></el-input>
                                        <div v-if="item.attrs.length">
                                            <div
                                                class="putt"
                                                v-for="(subitem,i) in item.attrs"
                                                :key="i"
                                            >
                                                <el-input
                                                    v-model="subitem.attr"
                                                    style="width:100px"
                                                    placeholder=""
                                                    clearable
                                                ></el-input>
                                                <div class="close" @click="closeAtrr(index,i)"></div>
                                            </div>
                                        </div>
                                        <el-button
                                            type="primary"
                                            icon="el-icon-circle-plus-outline"
                                            @click="addAtrr(index)"
                                        ></el-button>
                                    </li>
                                </ul>
                                <div class="add">
                                    <el-button type="primary" @click="addStand"></el-button>
                                    <el-button type="primary" @click="getTable">sku</el-button>
                                    <el-button type="primary" @click="read(false)">sku</el-button>
                                </div>
                            </div>
                            <div class="table">
                                <el-table
                                    v-if="isTable"
                                    :data="tableData"
                                    border
                                    style="width: 100%"
                                >
                                    <el-table-column prop="skuValue" label="sku" min-width="100"></el-table-column>
                                    <el-table-column label="" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.price"
                                                placeholder="sku"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.promotionPrice"
                                                placeholder="sku"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.balance"
                                                placeholder=""
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="" min-width="80">
                                        <template slot-scope="{row}">
                                            <el-input
                                                v-model="row.quantity"
                                                placeholder="sku"
                                                clearable
                                            ></el-input>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </div>
                        </div>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="onSubmit(false)"></el-button>
                        <el-button type="primary" @click="onSubmit(true,1)"></el-button>
                        <el-button type="primary" @click="onSubmit(true,0)"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "goodsEdit",
    data() {
        return {
            carousel:[],//
            imageText: [],//
            

            inputVisible: false, //input
            inputValue: "", //input
            parames: {
                title: "",
                goodsId:"",
                pictUrl: "", //
                picCharts: [], //
                picDetails: [], //
                reservePrice: "", //
                finalPrice: "", //
                tags: [], //
                categoryIds: [1], //
                currencyType: "1", //
                status: 1, //0 1  2 3
                onSaleTime: "", //
                quantity: "", //
                goodsPropertyRefList: [],
                goodsSkuList: [],
                publisher:"",
                author:"",
                freightPrice:"",//
                freePrice:"",//
                balance:"",//
            },
            noPostage:true,//
            categoryOptions: [],
            //
            moneyOptions: [
                {
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                }
            ],
            //sku
            tableData: [],
            input: "",
            isTable: false,
            standData: [],
            list: [],
            group: []
        };
    },
    created() {
        this.getCategoryList();
        this.getGoodsDetail()
    },
    methods: {
        //
        async getGoodsDetail() {
            const data = await this.$api.GoodsDetail({
                goodsId:this.$route.query.goodsId
            },this.$route.query.goodsId);
            this.parames.title=data.goodsTitle
            this.parames.goodsId=data.goodsId
            this.parames.pictUrl=data.goodsImg
            this.parames.picCharts=data.picCharts
            // 
            if(data.picCharts.length>0){
                let picCharts=data.picCharts
                for(let i in picCharts){
                    let obj={
                        url:picCharts[i]
                    }
                    this.carousel.push(obj)
                }
            }
            if(data.picDetails.length>0){
                let picDetails=data.picDetails
                for(let i in picDetails){
                    let obj={
                        url:picDetails[i]
                    }
                    this.imageText.push(obj)
                }
            }
            this.parames.picDetails=data.picDetails
            this.parames.reservePrice=data.reservePrice
            this.parames.finalPrice=data.finalPrice
            this.parames.categoryIds=data.categoryIds
            this.parames.currencyType=data.currencyType
            this.parames.status=data.goodsStatus
            if( (new Date()).getTime()>(new Date(data.onSaleTime)).getTime()){
                this.parames.onSaleTime=""
            }else{
                this.parames.onSaleTime=(new Date(data.onSaleTime)).getTime()
            }
            if (data.tags) {
              this.parames.tags = data.tags.split(",")
            }
            this.parames.quantity=data.quantity
            this.parames.balance=data.balance
            this.parames.publisher=data.publisher
            this.parames.author=data.author
            //sku 
            if(data.goodsPropertyRefList!=null&&data.goodsSkuList.length>0){
                let arr = data.goodsSkuList
                let newArr = []
                let table = [];
                for(let i in arr){
                    newArr.push(`${arr[i].skuValueInfo};`)
                    table.push({
                        skuValue: `${arr[i].skuValueInfo};`,
                        price: arr[i].price,
                        quantity: arr[i].quantity,
                        promotionPrice: arr[i].promotionPrice,
                        balance:arr[i].balance
                    });
                }
                this.read(true,newArr)
                this.tableData = table;
                this.isTable = true;
            }

        },
        //
        async getCategoryList() {
            const data = await this.$api.CategoryList({
                pageNo: 1,
                pageSize: 100
            });
            this.categoryOptions = data;
        },
        //
        async addCategory(categoryName) {
            const data = await this.$api.AddCategory({
                categoryName,
                sortOrder: 1
            });
            this.getCategoryList();
        },
        //
        async editGoods(categoryName) {
            const data = await this.$api.GoodsEdit(this.parames);
            //
            this.$router.push({
                name: "goodsList",
                params: {
                    onload: true,
                }
            })
        },
        //
        showCategoryBox() {
            this.$prompt("", "", {
                confirmButtonText: "",
                cancelButtonText: ""
            })
                .then(({ value }) => {
                    if (value === "") {
                        this.$message({
                            message: "",
                            type: "warning"
                        });
                    } else {
                        this.addCategory(value);
                    }
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        handleClose(tag) {
            this.parames.tags.splice(this.parames.tags.indexOf(tag), 1);
        },
        //
        showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        //
        handleInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
                this.parames.tags.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = "";
        },
        //
        uploadSuccess(imgUrl, index) {
            if (index == 1) {
                this.parames.pictUrl = imgUrl;
            } else if (index == 2) {
                this.parames.picCharts.push(imgUrl);
            } else if (index == 3) {
                this.parames.picDetails.push(imgUrl);
            }
        },
        //
        removeRemove(imgUrl, index) {
            if (index == 2) {
                this.parames.picCharts.splice(
                    this.parames.picCharts.findIndex(item => item === imgUrl),
                    1
                );
            } else if (index == 3) {
                this.parames.picDetails.splice(
                    this.parames.picDetails.findIndex(item => item === imgUrl),
                    1
                );
            }
        },

        // 
        addStand(i) {
            //  4
            if (this.standData.length > 3) {
                this.$message("");
            } else {
                this.standData.push({ title: "", attrs: [] });
            }
        },
        // sku
        getTable() {
            this.isTable = true;
            this.tableData = [];
            this.group = [];
            this.list = [];

            const num = this.standData.length;
            this.standData.forEach(item => {
                this.list.push(item.attrs);
            });

            var arr = [];
            var that = this;
            function func(skuarr = [], i) {
                for (let j = 0; j < that.list[i].length; j++) {
                    if (i < that.list.length - 1) {
                        skuarr[i] = that.list[i][j].attr;
                        func(skuarr, i + 1);
                    } else {
                        arr.push([...skuarr, that.list[i][j].attr]);
                    }
                }
                return arr;
            }
            let newList = func([], 0);
            let b;
            newList.forEach(item => {
                b = "";
                for (let i = 0; i < num; i++) {
                    let a = this.standData[i].title;
                    a = a + ":" + item[i];
                    b = b + a + ";";
                }
                b = b.substring(0, b.length - 1);
                this.group.push(b);
            });
            let table = [];
            for (let j = 0; j < this.group.length; j++) {
                table.push({
                    skuValue: this.group[j],
                    price: "",
                    quantity: "",
                    promotionPrice: "",
                    balance:""
                });
            }
            this.tableData = table;
        },

        // 
        remove(i) {
            this.standData.splice(i, 1);
        },
        // 
        addAtrr(i) {
            //  5
            if (this.standData[i].attrs.length > 4) {
                this.$message("5");
            } else {
                // 
                this.standData[i].attrs.push({ attr: "" });
            }
        },
        // 
        closeAtrr(a, b) {
            this.standData[a].attrs.splice(b, 1);
        },

        // 
        read(status,data) {
            // arr [":;c:1;", ":;c:2;"]
            let arr = []
            if(status){
                arr = data
            }else{
                arr = [":;", ":;"];
            }
            
            const a = arr[0].split(";");
            const num = a.length - 1;
            let ss = [];
            for (let tt = 0; tt < num; tt++) {
                ss.push([]);
            }

            arr.forEach(item => {
                for (let tt = 0; tt < num; tt++) {
                    ss[tt].push(item.split(";")[tt].split(":")[1]);
                }
            });
            ss = ss.map(item => {
                return Array.from(new Set(item));
            });
            for (let s = 0; s < ss.length; s++) {
                for (let t = 0; t < ss[s].length; t++) {
                    ss[s][t] = { attr: ss[s][t] };
                }
            }
            for (let i = 0; i < num; i++) {
                this.standData.push({
                    title: a[i].split(":")[0],
                    attrs: ss[i]
                });
            }
        },

        onSubmit(type, status) {
            if (!type) {
                this.$router.go(-1);
                return;
            }
            this.parames.status = status;
            if (status == 0 && this.parames.onSaleTime == "") {
                this.$message.error("");
                return;
            }
            if (this.tableData.length > 0) {
                this.parames.goodsSkuList = this.tableData;
                for (let key in this.standData) {
                    let obj = {
                        propertyName: this.standData[key].title,
                        valueAliasList: []
                    };
                    for (let i in this.standData[key].attrs) {
                        obj.valueAliasList.push(
                            this.standData[key].attrs[i].attr
                        );
                    }
                    this.parames.goodsPropertyRefList.push(obj);
                }
            }
            this.editGoods();
        }
    },
    watch:{
        noPostage(n){
            if(n){
                this.parames.freightPrice=""
                this.parames.freePrice=""
            }
        }
    }
};
</script>

<style scoped >
ul {
    list-style: none;
    padding: 0;
}
ul li {
    display: flex;
    flex-wrap: wrap;
}
.attr {
    margin-bottom: 10px;
}
.el-input {
    width: auto;
}
.putt {
    display: inline-block;
    position: relative;
    margin-right: 10px;
}

.title {
    background-color: bisque;
    margin-right: 10px;
}
.close {
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: red;
    color: #fff;
    border-radius: 50%;
    line-height: 15px;
    text-align: center;
    right: -5px;
    top: -5px;
}
</style>